@import "./variables";

.hero-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 120px);
  gap: 200px;
  padding: $l-spacing;

  .hero-main-text {
    color: white;
    font-weight: 300;
    font-size: clamp(1.9rem, 3vw, 5rem);
    animation: fadeInFromTop 1s ease-in-out;
  }

  .hero-secondary-text {
    font-family: $secondary-font-style;
    color: #00a3ff;
    font-size: clamp(0.9rem, 1.3vw, 2rem);
    font-weight: 300;
    margin-top: $s-spacing;
    animation: fadeInFromBottom 1s ease-in-out;
  }

  .hero-anim {
    width: 700px;
  }
}

@media (max-width: 1350px) {
  .hero-container {
    flex-direction: column;
    gap: 30px;
    text-align: center;

    .hero-anim {
      width: 500px;
    }
  }
}

@media (max-width: 600px) {
  .hero-container {
    .hero-anim {
      width: 300px;
    }
  }
}

@keyframes fadeInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes fadeInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
